.contact-us {
  &__container {
    @include content-padding-inline;
  }

  &__block {
    display: grid;
    margin-block: 48px;

    @include on-desktop {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 110px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 16px;

    margin-bottom: 48px;

    @include on-tablet {
      margin-bottom: 80px;
    }
  }

  &__input {
    height: 48px;
    padding-left: 16px;

    color: $main-text-color;

    background-color: $field-color;
    border: 1px solid $field-color;
    border-radius: 8px;

    &::placeholder {
      font-size: 14px;
      letter-spacing: 0.15px;
      color: $inactive-text-color;
    }

    &:hover {
      border-color: $hover-text-color;

      &::placeholder {
        color: $main-text-color;
      }
    }

    &:focus {
      outline-color: $accent-color;
    }
  }

  &__textarea {
    height: 148px;
    padding: 14px 16px;
    margin-bottom: 32px;
    resize: none;

    font-family: Manrope, sans-serif;
    color: $main-text-color;

    background-color: $field-color;
    border-color: $field-color;
    border-radius: 8px;

    &::placeholder {
      font-size: 14px;
      letter-spacing: 0.15px;
      color: $inactive-text-color;
    }

    &:hover {
      border-color: $hover-text-color;

      &::placeholder {
        color: $main-text-color;
      }
    }

    &:focus {
      outline-color: $accent-color;
    }
  }

  &__button {
    font-size: 16px;

    border: none;
  }

  &__content {
    &__block {
      margin-bottom: 24px;

      &-title {
        margin-bottom: 8px;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        color: $accent-text-color-gray;
      }

      &-text {
        color: $accent-text-color-dark;

        @include hover(color, $accent-color);
      }
    }
  }
}
